$animation-duration: 400ms;
$animation-name: switch;

:export {
  animationDuration: $animation-duration;
  animationName: $animation-name;
}

.wrapper {
  position: relative;
  height: 100%;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #181717A6;
  z-index: 2;
  transition: clip-path $animation-duration;
}

.panels {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.step1Panel1 {
  position: absolute;
  top: calc(66px * var(--size-ratio));
  left: 50%;
  transform: translateX(-50%);
  width: calc(312px * var(--size-ratio));
  z-index: 3;
}

.step1Panel2 {
  position: absolute;
  top: calc(470px * var(--size-ratio));
  left: 50%;
  transform: translateX(-50%);
  width: calc(312px * var(--size-ratio));
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
}

.step1Panel2NextButton {
  margin-top: calc(18px * var(--size-ratio));
}

.nextButtonIcon {
  width: calc(46px * var(--size-ratio));
  height: calc(16px * var(--size-ratio));
}

.step2Panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(312px * var(--size-ratio));
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
}

.step2PanelNextButton {
  margin-top: calc(21px * var(--size-ratio));
}

.step3Panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(312px * var(--size-ratio));
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
}

.step3PanelNextButton {
  margin-top: calc(21px * var(--size-ratio));
}

.step4Panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(312px * var(--size-ratio));
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
}

.step4PanelNextButton {
  margin-top: calc(21px * var(--size-ratio));
}

:global(.#{$animation-name}-enter) {
  .step1Panel1, .step1Panel2, .step2Panel, .step3Panel, .step4Panel {
    opacity: 0;
  }
}

:global(.#{$animation-name}-enter-active) {
  .step1Panel1, .step1Panel2, .step2Panel, .step3Panel, .step4Panel {
    opacity: 1;
    transition: opacity $animation-duration;
  }
}

:global(.#{$animation-name}-exit) {
  .step1Panel1, .step1Panel2, .step2Panel, .step3Panel, .step4Panel {
    opacity: 1;
  }
}

:global(.#{$animation-name}-exit-active) {
  .step1Panel1, .step1Panel2, .step2Panel, .step3Panel, .step4Panel {
    opacity: 0;
    transition: opacity $animation-duration;
  }
}